#mytable-5 {
    margin-left: 20%;
    margin-right: 20%;
    margin-top: 0;
    width: 60%;
    margin-bottom: 20px;
}

#inputfilechoose {
    width: 5rem;
    opacity: 0;
    position: relative;
    z-index: 1;
}

#inputfilechoose:active~.upload_button {
    transform: translateY(1px);
}

#uploadandsubmitblock {
    position: relative;
    display: flex;
}

.upload_button {
    width: 5rem;
    left: 0;
    color: white;
    border-radius: 5px;
    height: 2rem;
    position: absolute;
    background-color: #f55e30;
    border: none;
}

.submit_button {
    color: white;
    width: 5rem;
    height: 2rem;
    border-radius: 5px;
    margin-left: 1rem;
    background-color: #1E6A44 ;
    border: none;
}

button:active {
    transform: translateY(1px);
}

#mytableblock-1 {
    overflow-x: auto;
}

#mytable-1 {
    overflow-x: auto;
    
}

@media screen and (max-width : 1000px) {

    #mytable-1 {
        margin: 0 10%;
        width: 80%;
    }

}